.wheel {
  cursor: pointer;
  position: relative;
  width: 200px;
  height: 200px;
  background-color: white;
  border-radius: 50%;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset,
    rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}

